.button {
  height: 42px;
  text-align: center;
  z-index: 1;
  color: var(--button-text-color-first);
  background-color: var(--icon-bg-color);
  border: none;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  padding: 12px 35px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
  transition: color .25s cubic-bezier(.4, 0, .2, 1) .25s;
  display: inline-flex;
  position: relative;
  overflow: hidden;
}

.button:before {
  content: "";
  width: 200%;
  height: 150%;
  z-index: -1;
  background-color: var(--button-hover-color);
  transform-origin: 0 85%;
  transition: transform .5s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: -5px;
  left: -5px;
  transform: translateX(-120%)skewX(50deg);
}

.button:hover:before, .button:focus:before {
  transform: translateX(0)skewX(50deg);
}

.button:focus {
  color: var(--main-text-color);
}

.button--transparent {
  height: 42px;
  text-align: center;
  border: 2px solid var(--icon-bg-color);
  background-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  padding: 12px 35px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.button--transparent:hover, .button--transparent:focus {
  border-color: var(--button-hover-color);
}

.button--transparent:focus {
  color: var(--main-text-color);
}

.button--disabled {
  background-color: var(--third-text-color);
}

.button--disabled--transparent {
  border: 2px solid var(--third-text-color);
  background-color: rgba(0, 0, 0, 0);
}

.back-to-top {
  width: 50px;
  height: 50px;
  background-color: var(--icon-bg-color);
  border-radius: 50%;
  transition: box-shadow .5s cubic-bezier(.4, 0, .2, 1);
  display: none;
  position: fixed;
  bottom: 50px;
  right: 25px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.back-to-top:hover, .back-to-top:focus {
  outline: none;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, .12), 4px 4px 4px rgba(0, 0, 0, .16), 6px 6px 6px rgba(0, 0, 0, .2);
}

.back-to-top:before, .back-to-top:after {
  content: "";
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.back-to-top:before {
  width: 5px;
  height: 25px;
  border-color: var(--icon-bg-color);
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}

.back-to-top:after {
  width: 15px;
  height: 15px;
  border-top: 5px solid #629460;
  border-left: 5px solid #629460;
  transition: border-color .5s cubic-bezier(.4, 0, .2, 1);
  transform: translate(-50%, -50%)rotate(45deg);
}

.back-to-top:hover:after {
  border-top: 5px solid var(--main-text-color);
  border-left: 5px solid var(--main-text-color);
}

